
import { defineComponent, toRefs } from 'vue'
export default defineComponent({
  name: 'popUp',
  props: {
    show: Boolean,
    position: {
      type: String,
      default: 'center' // center, top, right, bottom, left
    }
  },
  setup (props, { emit }) {
    function handleHide () {
      emit('update:show', false)
      emit('hide', false)
    }
    return {
      handleHide,
      ...toRefs(props)
    }
  }
})
